<template>
  <div id="create-course">
    <div class="main">
      <div class="top">
        <div class="tt">创建课程</div>
      </div>
      <div class="concent">
        <el-form ref="form" :model="form" :rules="rules" label-width="80px" label-position="top">
          <el-form-item label="科目" prop="courseName">
            <el-input :disabled="editAble" v-model="form.courseName"></el-input>
          </el-form-item>
          <el-form-item label="班级" prop="classID">
            <el-select
              filterable
              v-model="form.classID"
              :disabled="editAble"
              placeholder="请选择班级"
              style="width:100%;"
              @change="selectClass"
            >
              <el-option
                v-for="(item, index) in classOptions"
                :key="index"
                :label="item.label"
                v-model="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="上课日期" prop="classDate">
            <el-date-picker
              v-model="form.classDate"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期"
              style="width:100%;"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="开始/结束时间" required>
            <el-col :span="11">
              <el-form-item prop="startTime">
                <el-time-picker
                  value-format="HH:mm"
                  format="HH:mm"
                  v-model="form.startTime"
                  :picker-options="startPickerOptions"
                  placeholder="选择时间"
                  style="width:100%;"
                ></el-time-picker>
              </el-form-item>
            </el-col>
            <el-col :span="2" style="text-align: center;">-</el-col>
            <el-col :span="11">
              <el-form-item prop="endTime">
                <el-time-picker
                  value-format="HH:mm"
                  format="HH:mm"
                  v-model="form.endTime"
                  :picker-options="endPickerOptions"
                  placeholder="选择时间"
                  style="width:100%;"
                ></el-time-picker>
              </el-form-item>
            </el-col>
          </el-form-item>

          <el-form-item label="上课地点" prop="classroomID">
            <el-select
              filterable
              v-model="form.classroomID"
              placeholder="请选择班级"
              style="width:100%;"
              @change="selectClassRoom"
            >
              <el-option
                v-for="(item, index) in classRoomOptions"
                :key="index"
                :label="item.label"
                v-model="item.value"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label>
            <el-button @click="preStep">返回</el-button>
            <el-button :loading="btnLoading" type="primary" @click="saveData('form')">
              <span v-if="form.courseID && form.courseID.length>10">提交修改</span>
              <span v-else>创建</span>
            </el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
  import edit from "@/mixin/edit";
  import { formatDate } from "@/utils/utils";

  export default {
    name: "",
    components: {},
    mixins: [edit],
    props: {},
    data() {
      return {
        editAble: this.$route.query.classId ? true : false,

        form: {
          courseName: this.$route.query.courseName || "",
          courseCode: "",
          classID: this.$route.query.classId || "",
          className: "",
          classroomID: "",
          classroomName: "",
          classDate: "",
          startTime: "",
          endTime: ""
        },
        rules: {
          courseName: {
            required: true,
            min: 1,
            max: 60,
            message: "长度在 1 到 60 个字符",
            trigger: "blur"
          },
          classID: {
            required: true,
            message: "请选择班级",
            trigger: "blur"
          },
          classroomID: {
            required: true,
            message: "请选择教室",
            trigger: "blur"
          },
          classDate: {
            required: true,
            message: "请选择上课日期",
            trigger: "blur"
          },
          startTime: {
            required: true,
            message: "请选择开始时间",
            trigger: "blur"
          },
          endTime: {
            required: true,
            message: "请选择结束时间",
            trigger: "blur"
          }
        },
        classOptions: [],
        classRoomOptions: []
      };
    },
    created() {
    },
    mounted() {
      this.$nextTick(() => {
        if (this.$route.query && this.$route.query.courseId) {
          this.form.courseID = this.$route.query.courseId;
          this.getData();
        }
      });
    },
    methods: {
      getData() {
        if (!this.form.courseID && this.form.courseID.length < 10) return;
        this.$http
          .get(this.$api.CourseDetail, { courseID: this.form.courseID })
          .then(res => {
            if (res.Result) {
              this.form.courseName = res.Data["CourseName"];
              this.form.courseCode = res.Data["CourseCode"];
              this.form.classID = res.Data["ClassID"];
              this.form.className = res.Data["ClassName"];
              this.form.classroomID = res.Data["ClassroomID"];
              this.form.classroomName = res.Data["ClassroomName"];
              this.form.classDate = formatDate(
                res.Data["ClassDate"],
                "yyyy-MM-dd"
              );
              this.form.startTime = res.Data["StartTime"];
              this.form.endTime = res.Data["EndTime"];
            }
          })
          .catch(err => {
          });
      },
      initDict() {
        this.getAllClass();
        this.getAllClassRoom();
      },
      getAllClass() {
        this.classOptions = [];
        this.$http
          .get(this.$api.ClsList)
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.classOptions.push({
                  label: item["ClassName"],
                  value: item["ClassID"]
                });
              });
            }
            if (this.form.classID) this.selectClass(this.form.classID);
          })
          .catch(err => {
          });
      },
      getAllClassRoom() {
        this.classRoomOptions = [];
        this.$http
          .get(this.$api.getAllClassRoom)
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.classRoomOptions.push({
                  label: item["ClsRoomName"],
                  value: item["ClsRoomID"]
                });
              });
            }
          })
          .catch(err => {
          });
      },
      selectClass(val) {
        let sclass = this.classOptions.find(r => r.value === val);
        this.form.className = sclass["label"] || "";
      },
      selectClassRoom(val) {
        let classRoom = this.classRoomOptions.find(r => r.value === val);
        this.form.classroomName = classRoom["label"] || "";
      },
      preStep() {
        this.$router.push("/main/teacherSpace/prepare/courseView");
      },
      saveData(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.btnLoading = true;
            this.$http
              .post(
                this.form.courseID && this.form.courseID.length > 10
                  ? this.$api.CourseUpdate
                  : this.$api.CourseAdd,
                {
                  ...this.form,
                  token: this.$http.getToken()
                }
              )
              .then(res => {
                if (res.Result) {
                  this.$message({
                    message: "操作成功！",
                    type: "success"
                  });
                  setTimeout(() => {
                    this.$router.push("/main/teacherSpace/prepare/courseView");
                  }, 200);
                } else if (res.message) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                }
                this.btnLoading = false;
              })
              .catch(err => {
                this.btnLoading = false;
              });
          } else {
            this.$message({
              showClose: true,
              message: "请输入正确格式",
              type: "error"
            });
            return false;
          }
        });
      }
    },
    computed: {
      startPickerOptions() {
        return {
          selectableRange:
            "00:00:00 - " +
            (this.form.endTime ? this.form.endTime + ":00" : "23:59:00")
        };
        // return {
        //   start: "00:00",
        //   // step: "00:01",
        //   end: this.form.endTime || "23:00"
        // };
      },
      endPickerOptions() {
        return {
          selectableRange:
            (this.form.startTime ? this.form.startTime + ":00" : "00:00:00") +
            " - 23:59:00"
        };
        // return {
        //   start: this.form.startTime || "00:00",
        //   // step: "00:01",
        //   end: "23:00"
        // };
      }
    },
    watch: {}
  };
</script>

<style lang='less' scoped>
  #create-course {
    width: 956px;

    .main {
      // height: 100%;
      background: #fff;
      padding: 20px;
      text-align: left;

      .tt {
        font-size: 14px;
        font-family: MicrosoftYaHei;
        font-weight: 400;
        color: rgba(51, 51, 51, 1);
        margin-bottom: 33px;
      }

      .concent {
        width: 320px;
        // height: 450px;
        //   border: 1px solid #000;
        margin: 0 auto;

        .item {
          font-size: 14px;
          color: #333;

          .name {
            font-size: 14px;
            color: #333;
            margin-bottom: 20px;
          }

          .input {
            input,
            .el-select {
              width: 319px;
              height: 40px;
              background: rgba(236, 241, 247, 1);
              opacity: 0.8;
              border-radius: 3px;
              border: none;
              margin-bottom: 20px;
            }

            .el-input__suffix {
              height: 40px;
            }

            //   .el-input__icon{
            //       line-height: 80px;
            //   }
          }
        }
      }
    }
  }
</style>



